import request from '@/http/index'

// 
export function updateMyProfile(data) {
  return request({
    url: '/api/v1/user',
    method: 'PATCH',
    data
  })
}

/**
 * 
 */
export function userApi() {
  return request({
    url: '/api/v1/user',
    method: 'get',
  })
}



