import request from '@/http/index'

// units teams groups
export const getMyUTG = query => {
  return request({
    url: '/api/v1/organization/children',
    method: 'get',
    params: query
  })
}
// 
export const getMyOrders = query => {
  return request({
    url: '/api/v1/orders',
    method: 'get',
    params: query
  })
}
// id
export const getOrdersById = (query,id) => {
  return request({
    url: '/api/v1/users/'+ id +'/orders',
    method: 'get',
    params: query
  })
}
// 
export const patchOrders = (query,data) => {
  return request({
    url: '/api/v1/orders/' + query,
    method: 'patch',
    data: data
  })
}
// 
export const getUserData = (query,id) => {
  return request({
    url: '/api/v1/user/data',
    method: 'get',
    params: query
  })
}
// 
export const getOrders = query => {
  return request({
    url: '/api/v1/orders',
    method: 'get',
    params: query
  })
}
//Organizational Chart
export function getOrganizeChart() {
  return request({
    url: '/api/v1/organization',
    method: 'get',
  })
}



